body {
  text-align: center;
  background-color: #282c34;
  padding: 10px;
}

.App-logo {
  height: 70px;
  width: 70px;
  border-radius: 50%;
  margin-top: 10px;
}

.card {
  background-color: #dadada;
  height: 200px;
  padding: 5px;
}

.App-title {
  font-size: calc(10px + 2vmin);
  color: #61fb9c;
}

.cards {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10px;
}
.button {
  padding: 10px;
  background-color: #61fb9c;
  text-decoration: none;
  color: #282c34;
  font-weight: bold;
  border-radius: 10px;
}
.button:focus {
  outline: none;
}


@media only screen and (max-width: 600px) {
  .cards {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 10px;
  }
}